<template>
  <div>
    <div class="canvas-wrapper">
      <canvas ref="imageCanvas" class="canvas-overlay"></canvas>
    </div>
  </div>
</template>

<script>
export default {
  name: "Image",
  props: {
    img_id: Number,
    img_filepath: String,
  },
  data: function () {
    return {
      sample: 7528,
      colors: [
        "#a71e4a", // [167,  30,  74],
        "#7d3a97", // [125,  58, 151],
        "#483290", // [ 72,  50, 144],
        "#2a92c6", // [ 29, 146, 198],
        "#63b247", // [ 99, 178,  71],
        "#cede35", // [206, 222,  53],
        "#f3ee3c", // [243, 238,  60],
        "#fabb18", // [250, 187,  24],
        "#f35524", // [243,  85,  36]
      ],
      infos: []
    };
  },
  computed: {
    canvas: function () {
      return this.$refs["imageCanvas"];
    },
    ctx: function () {
      return this.canvas.getContext("2d");
    },
    item: function () {
      return this.infos.filter((item) => item.image_id == this.img_id)[0];
    },
  },
  mounted() {
    console.log(process.env)
    fetch(`${process.env.BASE_URL}sample_infos.json`)
      .then((r) => r.json())
      .then(
        (json) => {
          this.infos = json;
          this.drawImage(this.img_filepath);
        },
        (response) => {
          console.log("Error loading json:", response);
        }
      );
  },
  methods: {
    update() {
      fetch(`${process.env.BASE_URL}sample_infos.json`)
        .then((r) => r.json())
        .then(
          (json) => {
            this.infos = json;
            this.drawImage(this.img_filepath);
          },
          (response) => {
            console.log("Error loading json:", response);
          }
        );
    },
    drawImage(filepath) {
      console.log(filepath);

      let bg = new Image();
      bg.src = filepath;
      bg.onload = () => {
        this.canvas.height = bg.height;
        this.canvas.width = bg.width;

        this.ctx.drawImage(bg, 0, 0);
        this.initial_state = bg;
      };
    },
    displayBoundingBox(indices) {
      for (let idx = 0; idx < indices.length; idx++) {
        const val = indices[idx];
        const bbox = this.item["bbox"][val];

        const x = parseInt(bbox[0]);
        const y = parseInt(bbox[1]);

        const width = parseInt(bbox[2]) - x;
        const height = parseInt(bbox[3]) - y;

        this.ctx.beginPath();
        this.ctx.strokeStyle = this.colors[idx];
        this.ctx.globalAlpha = 0.2;
        this.ctx.lineWidth = 3;
        this.ctx.rect(x, y, width, height);
        this.ctx.stroke();
      }

      this.ctx.globalAlpha = 1.0
    },
    highlightBoundingBox(indices, highlight_idx) {
      this.ctx.drawImage(this.initial_state, 0, 0);
      for (let idx = 0; idx < indices.length; idx++) {
        const val = indices[idx];
        const bbox = this.item["bbox"][val];
        if (val != highlight_idx) {
          continue;
        }

        const x = parseInt(bbox[0]);
        const y = parseInt(bbox[1]);

        const width = parseInt(bbox[2]) - x;
        const height = parseInt(bbox[3]) - y;

        this.ctx.beginPath();
        this.ctx.strokeStyle = this.colors[idx];

        if (val == highlight_idx) {
          this.ctx.globalAlpha = 1.0;
          this.ctx.lineWidth = 5;
        } else {
          this.ctx.globalAlpha = 0.3;
          this.ctx.lineWidth = 3;
        }

        this.ctx.rect(x, y, width, height);
        this.ctx.stroke();
      }
    },
    resetBoundingBox() {
      this.ctx.drawImage(this.initial_state, 0, 0);
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.canvas-overlay {
  position: relative;
  pointer-events: none;
}
</style>
