<template>
  <div ref="heatmap"></div>
</template>

<script>
import * as d3 from "d3";

export default {
  name: "Heatmap",
  props: {
    attention_filepath: String,
    img_id: Number,
    layer: Number,
    head: Number,
  },
  data: function () {
    return {
      margin: { top: 20, right: 10, bottom: 30, left: 60 },
    };
  },
  computed: {
    width: function () {
      return 350 - this.margin.left - this.margin.right;
    },
    height: function () {
      return 410 - this.margin.top - this.margin.bottom;
    },
    item: function () {
      return this.attentions.filter((item) => item.image_id == this.img_id)[0];
    },
  },
  mounted() {
    this.svg = d3
      .select(this.$refs.heatmap)
      .append("svg")
      .attr("width", this.width)
      .attr("height", this.height)
      .append("g")
      .attr("transform", `translate(${this.margin.left}, ${this.margin.top})`);

    this.fetchHeatmap();
  },
  methods: {
    update() {
      this.svg.selectAll("*").remove();
      this.fetchHeatmap();
    },
    fetchHeatmap() {
      this.svg
        .append("text")
        .attr("x", this.width / 2 / 1.5)
        .attr("y", this.height - 30)
        .attr("text-anchor", "middle")
        .style("font-size", "16px")
        .style("font-weight", "bold")
        .text(`Layer ${this.layer + 1} Head ${this.head + 1}`);

      fetch(this.attention_filepath)
        .then((r) => r.json())
        .then(
          (json) => {
            this.attentions = json;
            this.displayHeatmap(this.layer, this.head);
          },
          (response) => {
            console.log("Error loading json:", response);
          }
        );
    },
    formatHeatmap(x_labels, y_labels, scores) {
      var formatted_records = [];
      for (let idx = 0; idx < x_labels.length; idx++) {
        const x = x_labels[idx];

        for (let idy = 0; idy < y_labels.length; idy++) {
          const y = y_labels[idy];
          const record = {
            group: x,
            variable: y,
            value: scores[idx][idy],
          };

          formatted_records.push(record);
        }
      }

      return formatted_records;
    },
    displayHeatmap(layer, head) {
      this.data = this.formatHeatmap(
        this.item["x_labels"],
        this.item["y_labels"],
        this.item["scores"][layer][head]
      );

      // Labels of row and columns -> unique identifier of the column called 'group' and 'variable'
      const myGroups = Array.from(new Set(this.data.map((d) => d.group)));
      const myVars = Array.from(new Set(this.data.map((d) => d.variable)));

      // Build X scales and axis:
      const x = d3
        .scaleBand()
        .range([0, this.width - 60])
        .domain(myGroups)
        .padding(0.05);

      const xAxis = this.svg
        .append("g")
        .style("font-size", 15)
        .attr("transform", `translate(0, ${this.width})`)
        .call(d3.axisBottom(x).tickSize(0));

      // Build Y scales and axis:
      const y = d3
        .scaleBand()
        .range([this.width, 0])
        .domain(myVars)
        .padding(0.05);

      const yAxis = this.svg
        .append("g")
        .style("font-size", 15)
        .call(d3.axisLeft(y).tickSize(0)); // return xAxis line

      xAxis.select(".domain").remove();
      yAxis.select(".domain").remove();

      const myColor = d3.scaleLinear().domain([0, 1]).range(["white", "blue"]);

      const mouseover = (event) => {
        const target = event.target;

        d3.select(target).style("stroke", "black").style("opacity", 1);

        // Change Axis Color
        const xLabel = d3.select(target).attr("x-label");
        xAxis.selectAll(".tick").each((d) => {
          if (xLabel == d) {
            d3.select(target).selectAll("text").style("fill", "red");
            this.$parent.mouseenter(this.item["x_labels"], d);
          } else {
            d3.select(target).selectAll("text").style("fill", "black");
          }
        });

        const yLabel = d3.select(target).attr("y-label");
        yAxis.selectAll(".tick").each((d) => {
          // d => value, i => index of the value
          if (yLabel == d) {
            d3.select(target).selectAll("text").style("fill", "red");
          } else {
            d3.select(target).selectAll("text").style("fill", "black");
          }
        });
      };

      const mouseleave = (event) => {
        d3.select(event.target).style("stroke", "none").style("opacity", 0.8);
        d3.selectAll("text").style("fill", "black");

        this.$parent.mouseleave();
      };

      // add the squares
      this.svg
        .selectAll()
        .data(this.data, function (d) {
          return d.group + ":" + d.variable;
        })
        .join("rect")
        .attr("x", function (d) {
          return x(d.group);
        })
        .attr("y", function (d) {
          return y(d.variable);
        })
        .attr("x-label", function (d) {
          return d.group;
        })
        .attr("y-label", function (d) {
          return d.variable;
        })
        .attr("rx", 4)
        .attr("ry", 4)
        .attr("width", x.bandwidth())
        .attr("height", y.bandwidth())
        .style("fill", function (d) {
          return myColor(d.value);
        })
        .style("stroke-width", 4)
        .style("stroke", "none")
        .style("opacity", 0.8)
        .on("mouseover", mouseover)
        .on("mouseleave", mouseleave);
      this.$parent.displayBoundingBox(this.item["x_labels"]);
    },
  },
};
</script>

