<template>
  <div>
    <div class="mt-2 mb-2">
      <div><b>1st Model Selector</b></div>

      <div>
        <button
          v-for="(model, idx) in models"
          v-bind:key="model"
          :class="{
            'btn-light': idx != model1_idx,
            'btn-info': idx == model1_idx,
          }"
          type="button"
          class="btn m-1 mt-3 mb-3"
          @click="onModel1Select(idx)"
        >
          {{ model }}
        </button>
      </div>
    </div>
    <div class="mb-2">
      <div><b>2nd Model Selector</b></div>

      <div>
        <button
          v-for="(model, idx) in models"
          v-bind:key="model"
          :class="{
            'btn-light': idx != model2_idx,
            'btn-info': idx == model2_idx,
          }"
          type="button"
          class="btn m-1 mt-3 mb-3"
          @click="onModel2Select(idx)"
          :disabled="idx == model1_idx"
        >
          {{ model }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ModelSelector",
  components: {},
  data: function () {
    return {
      models: ["VisualBERT", "VilBERT"],
      model1_idx: 0,
      model2_idx: 1,
    };
  },
  computed: {
    model1: function () {
      return this.models[this.model1_idx];
    },
  },
  methods: {
    onModel1Select: function (idx) {
      this.model1_idx = idx;
    },
    onModel2Select: function (idx) {
      this.model2_idx = idx;
    },
  },
};
</script>

