<template>
  <div>
    <div><b>Meme Selection</b></div>
    <div class="row mt-3">
      <div class="col-sm-5">Memes</div>

      <div class="col-sm-7">Comparison</div>
    </div>
    <hr />
    <div v-for="(sample, idx) in samples" v-bind:key="sample">
      <div class="row align-items-center mt-3 mb-3">
        <div class="col-sm-6">
          <img class="thumbnail" v-bind:src="sample_filepaths[idx]" />
          <div v-if="idx != selected_idx">
            <b>Similarity (SSIM): {{ similarities[samples[selected_idx]][sample] }}</b>
          </div>

          <button
            v-if="idx == selected_idx"
            type="button"
            class="btn btn-info m-1"
          >
            {{ sample }}
          </button>
          <button
            v-else
            type="button"
            class="btn btn-outline-dark m-1"
            v-on:click="onSelect(idx)"
          >
            {{ sample }}
          </button>
        </div>
        <div class="col-sm-6">
          <b>Label: {{ getSampleLabel(sample) }}</b>
          <br />
          <br />
          <span :class="{ 'text-danger': getDifference(sample, 'VisualBERT') }"
            >VisualBERT
            {{ labels_mapping[this.predictions[sample]["VisualBERT"]] }}</span
          >
          <br />
          <span :class="{ 'text-danger': getDifference(sample, 'VilBERT') }">
            VilBERT:
            {{ labels_mapping[this.predictions[sample]["VilBERT"]] }}</span
          >
        </div>
      </div>
      <hr v-if="idx != 3" />
    </div>
  </div>
</template>

<script>
export default {
  name: "MemeSelector",
  components: {},
  data: function () {
    return {
      publicPath: process.env.BASE_URL,
      sample_filepaths: [
        `${process.env.BASE_URL}images/07528.png`,
        `${process.env.BASE_URL}images/46518.png`,
        `${process.env.BASE_URL}images/80597.png`,
        `${process.env.BASE_URL}images/84762.png`,
      ],
      samples: [7528, 46518, 80597, 84762],
      similarities: {
        7528: {
          46518: 0.124,
          80597: 0.1,
          84762: 0.127,
        },
        46518: {
          7528: 0.124,
          80597: 0.07,
          84762: 0.117,
        },
        80597: {
          46518: 0.07,
          7528: 0.1,
          84762: 0.084,
        },
        84762: {
          46518: 0.117,
          80597: 0.084,
          7528: 0.127,
        },
      },
      predictions: {
        7528: {
          label: 1,
          VisualBERT: 1,
          VilBERT: 1,
        },
        46518: {
          label: 1,
          VisualBERT: 0,
          VilBERT: 1,
        },
        80597: {
          label: 1,
          VisualBERT: 1,
          VilBERT: 0,
        },
        84762: {
          label: 1,
          VisualBERT: 0,
          VilBERT: 0,
        },
      },
      labels_mapping: {
        0: "Not Hateful",
        1: "Hateful",
      },
      selected_idx: 0,
    };
  },
  computed: {
    selected_sample: function () {
      return this.samples[this.selected_idx];
    },
    selected_sample_filepath: function () {
      return this.sample_filepaths[this.selected_idx];
    },
  },
  methods: {
    onSelect: function (idx) {
      this.selected_idx = idx;
      this.$parent.showMeme(
        this.samples[this.selected_idx],
        this.sample_filepaths[this.selected_idx]
      );
    },
    getSampleLabel: function (sample) {
      return this.labels_mapping[this.predictions[sample]["label"]];
    },
    getDifference: function (sample, model_name) {
      return (
        this.labels_mapping[this.predictions[sample]["label"]] !=
        this.labels_mapping[this.predictions[sample][model_name]]
      );
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.thumbnail {
  max-width: 150px;
  max-height: 150px;
}
</style>
