<template>
  <div>
    <div class="mt-2 mb-2">
      <div><b>Model Selector</b></div>

      <div>
        <button
          v-for="(model, idx) in models"
          v-bind:key="model"
          :class="{
            'btn-light': idx != selected_idx,
            'btn-info': idx == selected_idx,
          }"
          type="button"
          class="btn m-1 mt-3 mb-3"
          @click="onSelect(idx)"
        >
          {{ model }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ModelSelector",
  components: {},
  data: function () {
    return {
      models: ["VisualBERT", "VilBERT"],
      selected_idx: 0,
    };
  },
  computed: {
    model1: function () {
      return this.models[this.selected_idx];
    },
  },
  methods: {
    onSelect: function (idx) {
      this.selected_idx = idx;
    },
  },
};
</script>

