<template>
  <div>
    <div><b>Meme Selection</b></div>

    <div v-for="(sample, idx) in samples" v-bind:key="sample" class="mt-4 mb-4">
      <img class="thumbnail" v-bind:src="sample_filepaths[idx]" />
      <div >
        <b v-if="idx != selected_idx"
          >Similarity (SSIM):
          {{ similarities[samples[selected_idx]][sample] }}</b
        >
      </div>

      <button v-if="idx == selected_idx" type="button" class="btn btn-info m-1">
        {{ sample }}
      </button>
      <button
        v-else
        type="button"
        class="btn btn-outline-dark m-1"
        v-on:click="onSelect(idx)"
      >
        {{ sample }}
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "MemeSelector",
  components: {},
  data: function () {
    return {
      publicPath: process.env.BASE_URL,
      sample_filepaths: [
        `${process.env.BASE_URL}images/07528.png`,
        `${process.env.BASE_URL}images/46518.png`,
        `${process.env.BASE_URL}images/80597.png`,
        `${process.env.BASE_URL}images/84762.png`,
      ],
      samples: [7528, 46518, 80597, 84762],
      similarities: {
        7528: {
          46518: 0.124,
          80597: 0.1,
          84762: 0.127,
        },
        46518: {
          7528: 0.124,
          80597: 0.07,
          84762: 0.117,
        },
        80597: {
          46518: 0.07,
          7528: 0.1,
          84762: 0.084,
        },
        84762: {
          46518: 0.117,
          80597: 0.084,
          7528: 0.127,
        },
      },
      selected_idx: 0,
    };
  },
  computed: {
    selected_sample: function () {
      return this.samples[this.selected_idx];
    },
    selected_sample_filepath: function () {
      return this.sample_filepaths[this.selected_idx];
    },
  },
  methods: {
    onSelect: function (idx) {
      this.selected_idx = idx;
      this.$parent.showMeme(
        this.samples[this.selected_idx],
        this.sample_filepaths[this.selected_idx]
      );
    },
  },
};
</script>

