<template>
  <div class="card border-dark m-4 p-1">
    <div class="card-header">Visualisation Demonstrations (Model Comparison)</div>
    <div class="card-body">
      <ComparisonModelSelector></ComparisonModelSelector>
      <hr />
      <div class="row mt-2">
        <div class="col-sm-4" style="border-right: 1px solid #cbcfd4">
          <ComparisonMemeSelector></ComparisonMemeSelector>
        </div>
        <div class="col-sm-8">
          <b>Cross-Modality Visualisation</b>
          <Image
            ref="image"
            v-bind:img_id="img_id"
            v-bind:img_filepath="img_filepath"
            class="mt-3 mb-3"
          ></Image>
          <div class="row">
            <div class="col-sm">
              <b>VisualBERT</b>
              <Heatmap
                v-for="idx in [...Array(2).keys()]"
                v-bind:key="idx"
                :ref="'heatmap_' + idx"
                v-bind:layer="layer"
                v-bind:head="idx"
                v-bind:img_id="img_id"
                v-bind:attention_filepath="attention_filepaths['VisualBERT']"
              ></Heatmap>
            </div>
            <div class="col-sm">
              <b>VilBERT</b>
              <Heatmap
                v-for="idx in [...Array(2).keys()]"
                v-bind:key="idx"
                :ref="'heatmap_' + idx"
                v-bind:layer="layer"
                v-bind:head="idx"
                v-bind:img_id="img_id"
                v-bind:attention_filepath="attention_filepaths['VilBERT']"
              ></Heatmap>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Image from "./Image.vue";
import Heatmap from "./Heatmap.vue";

import ComparisonModelSelector from "./ComparisonModelSelector.vue";
import ComparisonMemeSelector from "./ComparisonMemeSelector.vue";

export default {
  name: "Demo",
  components: {
    Image,
    Heatmap,
    ComparisonModelSelector,
    ComparisonMemeSelector,
  },
  data: function () {
    return {
      layer: 3,

      img_id: 7528,
      img_filepath: `${process.env.BASE_URL}images/07528.png`,

      attention_filepaths: {
        'VisualBERT': `${process.env.BASE_URL}sample_attentions_visual_bert_coco.json`,
        'VilBERT': `${process.env.BASE_URL}sample_attentions_visual_bert.json`,
      }
    };
  },
  methods: {
    mouseenter: function (indices, segment_idx) {
      this.$refs.image.highlightBoundingBox(indices, segment_idx);
    },
    mouseleave: function () {
      this.$refs.image.resetBoundingBox();
    },
    displayBoundingBox: function (indices) {
      setTimeout(this.$refs.image.displayBoundingBox(indices), 6000);
    },
    showMeme: function (img_id, img_filepath) {
      console.log(img_id, img_filepath);
      console.log(this.$refs);

      this.img_id = img_id;
      this.img_filepath = img_filepath;

      this.$refs.image.update();

      this.$refs.heatmap_0[0].update();
      this.$refs.heatmap_1[0].update();
      // this.$refs.heatmap_2[0].update();
    },
  },
};
</script>

